.card {
    border: 4px solid;
    border-radius: 2px;
    width: 160px;
    height: 160px;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 2px 2px 5px rgb(56, 56, 56);
            box-shadow: 2px 2px 5px rgb(56, 56, 56);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background:#ffffff;
  }
  
  .card > .img-container > img {
    height: 100%;
  }
  
  .card:hover {
    -webkit-box-shadow: 8px 8px 20px rgb(56, 56, 56);
            box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(4px, 4px);
        -ms-transform: translate(4px, 4px);
            transform: translate(4px, 4px);
  }


